import {Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {DocumentTypeService} from '../../../../services/document-types.service';
import {Constants} from '../../../../app.constants';

@Component({
    selector: 'app-add-document-type',
    templateUrl: './add-document-type.component.html',
    styleUrls: ['./add-document-type.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class AddDocumentTypeComponent implements OnInit {
    public NewTypeForm: FormGroup;
    errorMessage: any[];
    loading: boolean;

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private documentTypeService: DocumentTypeService) {
    }

    ngOnInit() {
        this.buildForm();
    }

    buildForm() {
        this.NewTypeForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            description: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            allowed: [0],
        });
    }

    onSubmit({value, valid}: { value: DocumentType, valid: boolean }) {
        if (value) {
            this.loading = true;
            this.documentTypeService.addDocType(value).subscribe((document_type: DocumentType) => {
                if (document_type) {
                    this.router.navigate(['/app/document-types'])
                }
                this.loading = false;
            }, (err) => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            })
        }
    }
}
