import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import {Constants} from '../../../app.constants';
import {DocumentTypeService} from '../../../services/document-types.service';
import Swal from 'sweetalert2';
import {FormArray, FormControl} from '@angular/forms';
import {DocumentType} from '../../../interfaces/interfaces';

@Component({
    selector: 'app-document-types',
    templateUrl: './document-types.component.html',
    styleUrls: ['./document-types.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class DocumentTypesComponent implements OnInit {
    public doc_types: DocumentType[] = [];
    toggle = false;
    loading: boolean;
    errorMessage: any;

    constructor(public documentTypeService: DocumentTypeService,
                private route: ActivatedRoute, private _Location: Location,
                private router: Router) {
    }

    ngOnInit() {
        this.getDocTypes('');
    }

    checkAll(ev) {
        this.doc_types.forEach(x => x.selected = ev.target.checked)
    }

    get isAllChecked() {
        return this.doc_types.every(_ => _.selected);
    }

    get hasSomeChecked() {
        return this.doc_types.find(e => e.selected);
    }

    public getDocTypes(filter): void {
        this.loading = true;
        this.doc_types = [];
        this.documentTypeService.getDocumentTypes(filter).subscribe((doc_types: DocumentType[]) => {
                this.doc_types = doc_types;
                this.loading = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.loading = false;
            }
        );
    }

    deleteDocType(id) {

        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete the selected record?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.documentTypeService.deleteDocumentType(id).subscribe((status: boolean) => {
                    if (status) {
                        Constants.succeeded('Document Type has been deleted!')
                        window.setTimeout(function () {
                            location.reload();
                        }, 2000)
                    } else {
                        Constants.error('Unable to delete Document Type');
                    }
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                });
            }
        })
    }

}
