import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {DocumentTypeService} from '../../../../services/document-types.service';
import {Constants} from '../../../../app.constants';
import {DocumentType} from '../../../../interfaces/interfaces';

@Component({
    selector: 'app-edit-document-type',
    templateUrl: './edit-document-type.component.html',
    styleUrls: ['./edit-document-type.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class EditDocumentTypeComponent implements OnInit {
    public TypeForm: FormGroup;
    doc_type: DocumentType = {
        name: '',
        description: '',
    }
    doc_types: DocumentType[] = [];
    errorMessage: any;
    loading: boolean;
    pageLoading: boolean;
    onSuccess: any;
    deleteMessageEnabled: boolean;

    constructor(
        private formBuilder: FormBuilder,
        private router: Router,
        private route: ActivatedRoute,
        private documentTypeService: DocumentTypeService,
    ) {
    }

    ngOnInit() {
        this.buildForm();
        const id = this.route.snapshot.params['id'];
        this.getDocumentType(id);
    }

    buildForm() {
        this.TypeForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            description: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
        });
    }

    getDocumentType(id: number) {
        this.pageLoading = true
        this.documentTypeService.getDocumentTypeByID(id).subscribe((doc_type: DocumentType) => {
                this.doc_type = doc_type;
                this.TypeForm.patchValue({
                    name: this.doc_type.name,
                    description: this.doc_type.description,
                });
                this.pageLoading = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router, true);
                this.pageLoading = false;
            }
        );
    }


    onSubmit({value, valid}: { value: DocumentType, valid: boolean }) {
        value.id = this.doc_type.id;
        this.loading = true;
        if (value.id) {
            this.documentTypeService.updateDocumentType(value)
                .subscribe((doc_type: DocumentType) => {
                        if (this.doc_type) {
                            this.onSuccess = 'Document Type successfully updated'
                        }
                    },
                    (err) => {
                        this.errorMessage = <any>err.Staus;
                    },
                    () => {
                        this.loading = false;
                    }
                )
        }
    }

    onDelete(event: Event) {
        event.preventDefault();
        this.documentTypeService.deleteDocumentType(this.doc_type.id)
            .subscribe((status: boolean) => {
                    if (status) {
                        this.router.navigate(['/app/document-types']);
                    } else {
                        Constants.error('Unable to delete Document Type');
                    }
                },
                (err) => {
                    Constants.handleError(err, this.router, true);
                }
            )
    }

}
