import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {LoaderModule} from '../../../shared/loader/loader.module';
import {CustomErrorModule} from '../../../shared/custom-error/custom-error.module';
import {DocumentTypesComponent} from './document-types.component';
import {AddDocumentTypeComponent} from './add-document-type/add-document-type.component';
import {EditDocumentTypeComponent} from './edit-document-type/edit-document-type.component';

export const routes = [
    {path: '', component: DocumentTypesComponent},
    {path: 'create', component: AddDocumentTypeComponent},
    {path: 'edit/:id', component: EditDocumentTypeComponent},
];

@NgModule({
    declarations: [
        DocumentTypesComponent, AddDocumentTypeComponent, EditDocumentTypeComponent
    ],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        LoaderModule,
        CustomErrorModule
    ]
})
export class DocumentTypesModule {
}
